<template>
    <v-app>
        <v-row id="new-employee">
            <v-col>
                <h3>
                    New Employee
                </h3>
                <v-card>
                    <v-container>
                        <v-form @submit.prevent="saveEmployee">
                            <v-text-field v-model="employee_id" type="text" label="Employee Id"></v-text-field>
                            <v-text-field v-model="name" type="text" label="Name"></v-text-field>
                            <v-text-field v-model="dept" type="text" label="Department"></v-text-field>
                            <v-text-field v-model="position" type="text" label="Position"></v-text-field>
                            <v-btn type="submit" color="primary" class="mr-4" @click.stop="">Add Employee</v-btn>
                            <router-link to="/" class="btn grey">Back</router-link>
                        </v-form>
                    </v-container>
                </v-card>
            </v-col>
        </v-row>
    </v-app>
</template>

<script>
    import db from './firebaseinit'
    export default {
        name: "NewEmployee",
        data(){
            return{
                employee_id: null,
                name: null,
                dept: null,
                position: null
            }
        },
        methods:{
            async saveEmployee(){
               await db.collection("employees").add({
                   employee_id: this.employee_id,
                   name: this.name,
                   dept: this.dept,
                   position: this.position
               });
                this.$router.push('/')

            }
        }
    }
</script>

