<template>
    <v-app>
        <v-row id="new-employee">
            <v-col>
                <h3>
                    New Employee
                </h3>
                <v-card>
                    <v-container>
                        <v-form @submit.prevent="updateEmployee">
                            <v-text-field v-model="employee_id" type="text" label="Employee Id"></v-text-field>
                            <v-text-field v-model="name" type="text" label="Name"></v-text-field>
                            <v-text-field v-model="dept" type="text" label="Department"></v-text-field>
                            <v-text-field v-model="position" type="text" label="Position"></v-text-field>
                            <v-btn type="submit" color="primary" class="mr-4" @click.stop="">Update Employee</v-btn>
                            <router-link v-bind:to="{name:'view-employee', params: {employee_id: employee_id}}" class="btn grey">Back</router-link>
                        </v-form>
                    </v-container>
                </v-card>
            </v-col>
        </v-row>
    </v-app>
</template>

<script>
    import db from './firebaseinit'
    export default {
        name: "EditEmployee",
        data(){
            return{
                employee_id: null,
                name: null,
                dept: null,
                position: null
            }
        },
        mounted() {
            this.fetchData()
        },
        methods: {
            async fetchData(){
                let snapshot = await db.collection("employees").where("employee_id", "==", this.$route.params.employee_id).get()
                snapshot.forEach(doc => {
                    this.employee_id  = doc.data().employee_id
                    this.name = doc.data().name
                    this.dept = doc.data().dept
                    this.position = doc.data().position
                })
            },
            async updateEmployee(){
                let snapshot = await db.collection("employees").where("employee_id", "==", this.$route.params.employee_id).get()
                snapshot.forEach(doc  =>{
                    doc.ref.update({
                        employee_id: this.employee_id,
                        name: this.name,
                        dept: this.dept,
                        position: this.position
                    })
                })
                await this.$router.push({name:'view-employee', params: {employee_id: this.employee_id}})
            }
        }
    }
</script>

